<template>
  <div>

  
    
      <div class="text-right">
<v-btn @click="dialog=true" x-small color="#1d333e" style="color:white"> Kalan Sayılar ({{getNumbers.length}})</v-btn>
      </div>
        <v-card
   
            color="#385F73"
            dark
          >
            <v-card-title class="headline">
              Sizin Kartınız
            </v-card-title>

            <v-card-subtitle style="font-size:20px;margin:auto;">
               <div v-if="getActiveUser.bingo!=''" class="text-center">
                   {{getActiveUser.bingo}}
               </div>
               <div v-else class="text-center">
                       <v-btn @click="chooseCard">Kart Seç</v-btn>
               </div>
            </v-card-subtitle>

            <v-card-actions>
                 <div style="display:flex;justify-content:space-between;width:100%">
                  <small>Kazandığı Savaş : {{getActiveUser.won}}</small>
                  <small style="margin:0 5px">Kaybettiği Savaş : {{getActiveUser.loose}}</small>
              </div>
            </v-card-actions>
          </v-card>
          <users/>
      <v-dialog
        transition="dialog-bottom-transition"
        max-width="500"
        v-model="dialog"
      >

        <template v-slot:default="dialog">
          <v-card>
            <v-toolbar
            class="hanalei"
              :color="colors"
            >Henüz Çıkmamış Numaralar
            </v-toolbar>
            <v-card-text style="height:1200px">
              <ul class="text-h2 pa-12" style="position:relative">
                  <li class="bingo" style="float:right" v-for="(b,i) in getNumbers" :key="i">{{b}}</li>
              </ul>
            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn
                text
                @click="dialog = false"
              >Kapat</v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>
     
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import setCard from '../plugins/chooseCard'
import users from './users'
export default {
    components:{
        users
    },
    props:['colors'],
    computed:{
        ...mapGetters(['getNumbers','getActiveUser','getActiveRoom'])
    },
data(){
    return{
        dialog:false,
        cards:[],
        chooseDialog:false,
        rndChooseShowNumber:0,
        text:"",
        text2:"",
        text3:"",
        count:3
    }
},
methods:{
    chooseCard(){
        //let num = 15
       let cards = setCard()
        this.$api.put('Room/'+this.getActiveRoom.key+'/Users',this.getActiveUser.key,{...this.getActiveUser,bingo:cards.join(',')})
    },
    startGame(){
     //   this.getNumbers=[]
        this.setNumbers()
        this.$api.putNumber('Room/'+this.getActiveRoom.key+'/Numbers',{number:this.getNumbers.join(',')}).then(res=>{
            console.log(res);
        })
    },
setNumbers(){
       this.$store.state.numbers = []
for (let i = 1; i < 91; i++) {
       this.$store.state.numbers.push(i)
    }
    },
    listenBingo(){
    
    },
    selectNumber(){
        this.text = "Hazır Mıyız? Numara Çekiliyor.."
        this.text2 = "Sonucu Bekleyin.."
        this.text3 = ""
        this.count = 3
        this.chooseDialog=true
        let vm = this
       let interval = setInterval(() => {
            let number = Math.floor(Math.random() * this.getNumbers.length)
            vm.rndChooseShowNumber = number
        }, 100);
       setTimeout(() => {
             this.text = "Seçilen Numara.."
              this.text2 = "Kağıdın Kontrol Et. Ben Makineyim Hata yapmam ama yine de sen bak adet yerini bulsun."
            let numberx = Math.floor(Math.random() * this.getNumbers.length)
            console.log(numberx);
            clearInterval(interval)
         this.rndChooseShowNumber = numberx

        let pos = this.getNumbers.map(b=>Number(b)).indexOf(numberx)
        console.log(pos);
       this.$store.commit('setChoosedNumber',pos)
       clearInterval(interval)
       this.$api.putNumber('Room/'+this.getActiveRoom.key+'/Numbers',{number: this.getNumbers.join(",")})
         this.rndChooseShowNumber = numberx

         clearInterval(interval)
       setTimeout(() => {
            this.text3 = "Pencere Kapanıyor Lütfen Bekleyin..."
            let count = setInterval(() => {
                vm.count--;
            }, 1000);
              setTimeout(() => {
                 clearInterval(count)
                  this.chooseDialog=false
              }, 3000);
       }, 8000);
       }, 5000);
    }
},
created(){
    //this.setNumbers()
}
}
</script>

<style>
.bingo{
    padding: 10px;
    border-radius: 50%;
    background: turquoise;
    list-style-type: none;
    box-shadow: .5px 2px 2px 2px rgba(0, 0, 0, 0.401);
    margin: 10px;
    width: 40px;
    text-align: center;

}
.rndChoose{
    background:red;font-size:36px;padding:10px;border-radius:50%;width:100px!important;height:100px!important;color: white;   
}
</style>




