export default {
    get: (data)=>{
        var requestOptions = {
            method: 'GET',
            redirect: 'follow'
          };
          
        return  fetch(`https://tombala-real-time-default-rtdb.firebaseio.com/${data}.json`, requestOptions)
            .then(response => response.json())
            .then(result =>result)
            .catch(error => console.log('error', error));
    },
    put : (data,put,dat)=>{
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
var raw = JSON.stringify(dat);

var requestOptions = {
  method: 'PUT',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};
         return fetch(`https://tombala-real-time-default-rtdb.firebaseio.com/${data}/${put}.json`, requestOptions)
            .then(response => response.json())
            .then(result => result)
            .catch(error => console.log('error', error));
    },
    putNumber : (data,put)=>{
      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
var raw = JSON.stringify(put);

var requestOptions = {
method: 'PUT',
headers: myHeaders,
body: raw,
redirect: 'follow'
};
       return fetch(`https://tombala-real-time-default-rtdb.firebaseio.com/${data}.json`, requestOptions)
          .then(response => response.json())
          .then(result => result)
          .catch(error => console.log('error', error));
  },
    post: (url,data)=>{
        var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");

var raw = JSON.stringify(data);

var requestOptions = {
  method: 'POST',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};

return fetch(`https://tombala-real-time-default-rtdb.firebaseio.com/${url}.json`, requestOptions)
  .then(response => response.json())
  .then(result => result)
  .catch(error => console.log('error', error));
    },
    delete : (data)=>{
      var requestOptions = {
        method: 'DELETE',
        redirect: 'follow'
      };
      
      fetch("https://tombala-real-time-default-rtdb.firebaseio.com/"+data+".json", requestOptions)
        .then(response => response.text())
        .then(result => console.log(result))
        .catch(error => console.log('error', error));
    }
}