const second = 1000;
const minute = second * 60;
const hour = minute * 60;
const day = hour * 24;
let countDown = new Date('Jan 01, 2021 00:00:00').getTime()
 

 


  export default ()=>{
    let now = new Date().getTime()
    let distance = countDown - now;
    let days =   Math.floor(distance / (day))
    let hours =    Math.floor((distance % (day)) / (hour))
    let min =   Math.floor((distance % (hour)) / (minute))
    let seconds=  Math.floor((distance % (minute)) / second);
    if(distance<=0){
        return {type:"timeIsUp"}
    }
    else{
        return {type:"holdOn", days:days<10?`0${days}`:days,hours:hours<10?`0${hours}`:hours,min:min<10?`0${min}`:min,seconds:seconds<10?`0${seconds}`:seconds}
    }
  }
