<template>
  <div>
   <div>
        <div class="chat-header" :style="`background:${colors}`">
            <h2 class="hanalei">Muhteşem Tombala Chat Ekranı</h2>
        </div>
        <div class="chat">
        <ul>
            <li :style="i.special?`background:purple;border-radius:50rem;color:whitesmoke;text-align:center`:''" style="list-style-type: none;padding:5px;margin:5px" v-for="(i,j) in getChats" :key="j">
                <strong><small>{{i.sender}} :</small> </strong>
               <small>{{i.message}}</small></li>
        </ul>
        </div>
        <div class="chat-footer">
            <div style="position:relative;background:#1d333e">
                <input @keypress.enter="sendMessage" v-model="message" type="text" style="width:80%;position:absolute" >
                <button @click="sendMessage"  class="btn-send" style="position:absolute;right:10px;top:6px">Gönder</button>
            </div>
        </div>
   </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
props:['colors'],
computed:{
    ...mapGetters(['getChats','getActiveUser','getActiveRoom'])
},
data(){
    return{
        message:""
    }
},
methods: {
    sendMessage(){
        this.$api.post('Room/'+this.getActiveRoom.key+'/Chats',{ message:this.message,sender:this.getActiveUser.name, special:false}).then(()=>{
            this.message=""
        })
    }
}
}
</script>

<style>
.chat{
    background: whitesmoke;
    padding: 10px;
    overflow-x: scroll; 
    height: 60vh;
    width: 100%;
}
.chat-header{
    text-align: center;
    color: white;
    width: 100%;
    height: 5vh;

}
.chat-footer{
    width: 100%;
    height: 5vh;
    background:#1d333e;
    border-radius: 5px;
}
.chat-footer input {
    padding: 10px;
    font-size: 12px;
    background: #1d333e;
    color: white;

}
.chat-footer input:focus, chat-footer input:active {
border:none

}
.btn-send{
   border-radius:50rem;
    padding: 5px 15px;
    font-size: 12px;
    background: whitesmoke;
    max-width: 300px;
}
</style>