<template>
<div>
    <div>
     <v-col cols="12">
             <v-alert
      color="#C51162"
      dark
      icon="mdi-material-design"
      border="right"
    >
     
      <div class="countdown">
       <h1> HOŞ MU GELECEKSİN 2021 </h1>
    <div  class="text-center">
    <h2 >{{days}}</h2>  
    <div>Gün</div>  
    </div> 
    <div  class="text-center">
    <h2 >{{hours}}</h2>  
    <div>Saat</div>  
    </div> 
    <div class="text-center">
    <h2 >{{minutes}}</h2>  
    <div>Dakika</div>  
    </div>
    <div class="text-center">
    <h2 >{{seconds}}</h2>  
    <div>Saniye</div>  
    </div>  
    </div> 
    </v-alert>
     <v-alert
      :color="colors"
      dark
      icon="mdi-material-design"
      border="right"
    >
    <h1 class="countdown">MÜKKEMMEL HARKULADE TOMBALA GECESINE HOŞ GELDİNİZ!</h1>
    </v-alert>
        </v-col>
</div>
  <div class="room_choose">
       
      <h1 class="text-center hanalei">Merhaba {{getActiveUser.name}}</h1>
      <h3 class="hanalei">Oda Seç</h3>
     <v-row>
         <v-col cols="12" lg="6" md="6"  v-for="(r,i) in getRooms" :key="i">
              <v-card
    class="mx-auto"
    color="#f2e2cd"
    
    
  >
    <v-card-title>
      <v-icon
        large
        left
      >
        mdi-twitter
      </v-icon>
      <span class="title font-weight-light">Oda {{i+1}}</span>
    </v-card-title>
    <v-card-text class="headline font-weight-bold">
   <div style="display:flex;justify-content:space-between">
         <v-btn color="#fd0000" x-small :disabled="r.transaction.startGame">{{r.transaction.startGame?'Oyun Oynanıyor...':'Oyun Henüz Başlamadı.'}}</v-btn>
     <v-btn text x-small>Kalan Numara : {{r.roomNumberCount.length}}</v-btn>
     <v-btn text x-small>Son Numara : {{r.transaction.activeNumber==-1?'Yok':r.transaction.activeNumber}}</v-btn>
   </div>
     <div style="display:flex;justify-content:flex-end">
  <v-btn text x-small>Odadaki Kişi Sayısı : {{r.userData.length}}</v-btn>
   </div>
    </v-card-text>

    <v-card-actions>
      <v-list-item class="grow">
        <v-list-item-content>
          <v-list-item-title>
              <v-btn :disabled="r.transaction.startGame" @click="joinRoom(r)" text>Odaya Katıl</v-btn>
          </v-list-item-title>
        </v-list-item-content>

        
      </v-list-item>
    </v-card-actions>
  </v-card>
         </v-col>
     </v-row>
      <v-btn @click="createRoom">Oda Kur</v-btn>

  </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import countDown from '../plugins/countdown'
export default {
computed:{
    ...mapGetters(['getActiveUser','getRooms'])
},
data(){
    return{
         showDate: true,
      days:0,
      hours:0,
      minutes:0,
      seconds:0,
      colors:"#000",
      colorsHeader:"#000"
    }
},
methods: {
    joinRoom(r){
        this.$store.commit('setActiveRoom',r)
        this.$emit('choosedRoom',true)
    },
    setNumbers(){
       let numbers = []
for (let i = 1; i < 91; i++) {
       numbers.push(i)
    }
    return numbers
    },
    createRoom(){
        this.$api.post('Room',{Chats:[],Numbers:{number:this.setNumbers().join(',')},Users:[],transaction:{voting:false,activeNumber:-1,chooseCard:false,chooseNumber:false,startGame:false}}).then(res=>{
            this.$api.put('Room',res.name,{key:res.name,Chats:[],Numbers:{number:this.setNumbers().join(',')},Users:[],transaction:{  voting:false,  activeNumber:-1,chooseCard:false,chooseNumber:false,startGame:false}})
        })
        
    }
},
mounted () {
    let vm = this
     setInterval(() => {
     let result =  countDown()
    if(result.type=='timeIsUp'){
      vm.showDate = false
    }
    else{
      vm.showDate=true
      vm.days = result.days
      vm.hours = result.hours
      vm.minutes = result.min
      vm.seconds = result.seconds
    }
    }, 1000);
}
}
</script>

<style>

</style>