<template>
 <v-container>
     
      <v-row>
          <v-col cols="12" md="6"  v-for="(u,i) in getUsers" :key="i"   lg="6">
               <v-card
   
            :color="u.admin?'#ffaaaa':'#385F73'"
            
          >
            <v-card-title class="headline">
              {{u.name}} <small style="font-size:12px;margin-left:5px">  {{u.admin?' - Oda Yöneticisi':''}}</small>
            </v-card-title>

            <v-card-subtitle>
                 <div style="display:flex;justify-content:space-between;width:100%">
                  <small>Kazandığı Savaş : {{u.won==0?'Yok':u.won}}</small>
                  <small style="margin:0 5px">Kaybettiği Savaş : {{u.loose}}</small>
              </div>
               <div class="text-right">
                    <v-btn @click="kick(u)" text style="color:red">X Odadan At</v-btn>
                </div>
            </v-card-subtitle>

            <v-card-actions>
               
                  <div style="display:flex;justify-content:space-between;width:100%">
                <v-btn text @click="sendMessage(`${u.name} kullanıcısına çay gönderdi.`)">Çay Gönder</v-btn>
                <v-btn text @click="sendMessage(`${u.name} kullanıcısına şaka yaptı. Ehehehe çok komik.`)">Şaka Yap</v-btn>
                <v-btn text @click="sendMessage(`${u.name} kullanıcısına elma var yicen mi diye sordu. Bıçak ile elma uzattı`)">Elma Uzat</v-btn>
                  </div>
            </v-card-actions>
          </v-card>
          </v-col>
      </v-row>
      <v-dialog v-model="transaction.voting" persistent width="500">
          <v-card v-if="getVoting.length!=0" style="padding:10px">
              <div v-if="getVoting[0].user != getActiveUser.key">
                  <h2 class="hanalei text-center">{{getVoting[0].sender}} isimli kullanıcı {{getVoting[0].name}} isimli kullanıcıyı atmak için oylama başlattı.</h2>
              <h3 class="text-center">Kabul Edenler : {{getVoting[0].accept}} - Reddedenler : {{getVoting[0].deny}}</h3>
              <div class="text-center mt-3" v-if="getVoting[0].senderKey != getActiveUser.key && showVote">
                  <v-btn :disabled="!getVoting[0].key" small style="margin:0 5px" color="#2da842" @click="put(true)">Odadan At</v-btn>
                  <v-btn :disabled="!getVoting[0].key" small color="#ff0000" @click="put(false)">Reddet</v-btn>
              </div>
              </div>
              <div v-else>
                  <h1 class="hanalei text-center">Adına Oylama Başlatılmıştır. Lütfen Bekleyiniz...</h1>
                      <h3 class="text-center">Kabul Edenler : {{getVoting[0].accept}} - Reddedenler : {{getVoting[0].deny}}</h3>
              </div>
          </v-card>
      </v-dialog>
 </v-container>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters(['getUsers','getNumbers',"getActiveUser",'getActiveRoom',"transaction","getVoting"])
    },
data(){
    return{
        users:[],
        showVote:true
    }
},
watch:{
    'transaction.voting'(data){
        if(data){
            this.showVote = true
        }
    },
    
},
methods:{
    put(data){
        if(data){
            console.log(this.getVoting[0].accept,"ACCCCEPEPPT");
            let accept = this.getVoting[0].accept + 1
            this.$api.put('Room/'+this.getActiveRoom.key+'/Voting',this.getVoting[0].key,{...this.getVoting[0],accept:accept})
        }
        else{
             let deny = this.getVoting[0].deny + 1
            this.$api.put('Room/'+this.getActiveRoom.key+'/Voting',this.getVoting[0].key,{...this.getVoting[0],deny:deny})

        }
        this.showVote = false
    },
    kick(user){
         this.$api.post('Room/'+this.getActiveRoom.key+'/Voting', {user : user.key , accept:1 , deny : 0,name : user.name,sender:this.getActiveUser.name,senderKey: this.getActiveUser.key}).then((res)=>{
            this.$api.put('Room/'+res.name+'/Voting',{...this.getVoting[0],key:res.name})
              this.$api.putNumber('Room/'+this.getActiveRoom.key+'/transaction',{ ...this.transaction,voting:true})
              setTimeout(() => {
            if(this.getVoting[0].accept > this.getVoting[0].deny){
                this.$api.delete('Room/'+this.getActiveRoom.key+'/Users/'+this.getVoting[0].user)
                this.$api.delete('Room/'+this.getActiveRoom.key+'/Voting/'+res.name)
                 this.$api.putNumber('Room/'+this.getActiveRoom.key+'/transaction',{ ...this.transaction,voting:false})
            }
            else{
                 this.$api.putNumber('Room/'+this.getActiveRoom.key+'/transaction',{ ...this.transaction,voting:false})

            }
         }, 10000);
         })
         
    },
   sendMessage(data){
        this.$api.post('Room/'+this.getActiveRoom.key+'/Chats',{ message:data,sender:this.getActiveUser.name, special:true}).then(()=>{
            this.message=""
        })
    }
},
mounted(){
    console.log(this.getVoting);
}
}
</script>

<style>

</style>