<template>
  <div data-app id="app">
{{getUsers}} ------------------ {{getActiveUser}}
    <v-container fluid>
      <v-row>
        <v-col cols="12">
             <v-alert
      color="#C51162"
      dark
      icon="mdi-material-design"
      border="right"
    >
     
      <div class="countdown">
       <h1> HOŞ MU GELECEKSİN 2021 </h1>
    <div  class="text-center">
    <h2 >{{days}}</h2>  
    <div>Gün</div>  
    </div> 
    <div  class="text-center">
    <h2 >{{hours}}</h2>  
    <div>Saat</div>  
    </div> 
    <div class="text-center">
    <h2 >{{minutes}}</h2>  
    <div>Dakika</div>  
    </div>
    <div class="text-center">
    <h2 >{{seconds}}</h2>  
    <div>Saniye</div>  
    </div>  
    </div> 
    </v-alert>
     <v-alert
      :color="colors"
      dark
      icon="mdi-material-design"
      border="right"
    >
    <h1 class="countdown">MÜKKEMMEL HARKULADE TOMBALA GECESINE HOŞ GELDİNİZ!</h1>
    </v-alert>
        </v-col>
        <v-col cols="12">
          <div class="text-right ">
            <span class="hanalei">Hoş Geldin, {{username}}</span> <v-btn color="#2da842" style="color:white" @click="dialog=true" x-small> Adımı Değiştir</v-btn>
          </div>
        </v-col>
        <v-col cols="12">
           
      <v-btn v-if="!transaction.startGame&&getActiveUser.admin" @click="startGameDialog=true" small>Oyunu Başlat</v-btn>
      <v-btn v-if="transaction.startGame&&getActiveUser.admin" @click="put({startGame:false})" small>Oyunu Sonlandır</v-btn>
       <v-btn v-if="!transaction.chooseNumber&&transaction.startGame&&getActiveUser.admin" style="float:right;color:white" color="#ff0000" @click="put({chooseNumber:true})" small>Yeni Numara Seç</v-btn>
        </v-col>
        <v-col cols="12" md="3" lg="4">
          <Chat :colors="colorsHeader"/>
        </v-col>
        <v-col cols="12" md="9" lg="8">
          <Bingo :colors="colorsHeader"/>
        </v-col>
      </v-row>
    </v-container>
  <v-dialog
  transition="dialog-bottom-transition"
  persistent
      v-model="dialog"
      width="500"
    >

      <v-card>
        <v-card-title :style="`background:${colors}`" class="headline grey lighten-2">
       <p>  Kullanıcı Adı Belirleyin</p>
        </v-card-title>

        <v-card-text class="mt-3">
        Muhteşem Tombalaya katılmak için lütfen kullanıcı adını giriniz.
        </v-card-text>

        <v-divider></v-divider>
     <div class="pa-2">
        <v-text-field
            v-model="username"
            filled
            @keypress.enter="joinGame"
            label="Muhteşem Kullanıcı Adınız"
            clearable
          ></v-text-field>
     </div>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="writeUserData"
          >
            Oyuna Katıl
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
     <v-dialog v-model="transaction.chooseNumber" persistent width="400">
         <v-card>
            <div class="pa-2">
                <div   class="hanalei text-center">
                    {{text}}
                </div>
                  <div class="text-center mt-3" style="width:100%;height:100px">
              <span class="rndChoose">{{rndChooseShowNumber}}</span>
          </div>
          <div   class="hanalei text-center">
                    {{text2}}
                </div>
                  <div   class="hanalei text-center">
                    {{text3}}
                </div>
                 <h1 v-if="text3!=''" :style="'color:'+colors"  class="hanalei text-center">
                    {{count}}
                </h1>
                <div class="text-center">
              {{getActiveUser.bingo}}
           </div>
            </div>
           
         </v-card>
      </v-dialog>
      <v-dialog v-model="startGameDialog" width="400"   transition="dialog-bottom-transition">
         <v-card>
            <div class="pa-2">
                <div   class="hanalei text-center">
                  {{ allSelected?'Oyunu Başlatabilirsiniz. İyi Eğlenceler.':'Oyunu Başlatmak için Lütfen Bekleyin...'}}
                </div>
               
         <div v-for="(u,i) in getUsers" :key="i">
            <div   class=" text-center mt-3">
                   <strong> {{u.name}} <small style="color:red"> {{getActiveUser.key==u.key?'*Siz':''}}</small> <small v-if="u.admin" style="color:orange">* Oda Yöneticisi</small> </strong>
                </div>
                  <div   class="text-center">
                    {{u.bingo==''? "Kart Seçimi Yapıyor.. Lüften Bekleyin...":'Kart Seçimini Yaptı.'}}
                </div>
                <div v-if="getActiveUser.key==u.key && u.bingo==''" class="text-center">
                  <v-btn small color="#1d333e" style="color:white" @click="chooseCardMethods">KART SEÇ</v-btn>
                </div>
               
         </div>
          <div class="text-center mt-3">
             <v-btn text @click="startGameDialog=false">Kapat</v-btn>
                  <v-btn :disabled="!allSelected" color="#1d333e" style="color:white" @click="allSelected?put({startGame:true}):''">Oyunu Başlat</v-btn>
                 

           </div>
            </div>
           
         </v-card>
      </v-dialog>
       <v-dialog v-model="userStartGame" width="400" persistent   transition="dialog-bottom-transition">
         <v-card>
            <div class="pa-2">
                <div   class="hanalei text-center">
                  {{ allSelected?'Tüm Oyuncular Kartlarını seçti. Oda Yöneticisinin oyunu başlatmasını bekleyiniz...':'Tüm oyuncuların kart seçmesi bekleniyor...'}}
                </div>
               
         <div v-for="(u,i) in getUsers" :key="i">
            <div   class=" text-center mt-3">
                   <strong> {{u.name}} <small style="color:red"> {{getActiveUser.key==u.key?'*Siz':''}}</small>  <small v-if="u.admin" style="color:orange">* Oda Yöneticisi</small></strong>
                </div>
                  <div   class="text-center">
                    {{u.bingo==''? "Kart Seçimi Yapıyor.. Lüften Bekleyin...":'Kart Seçimini Yaptı.'}}
                </div>
                  <div v-if="getActiveUser.key==u.key && u.bingo==''" class="text-center">
                  <v-btn small color="#1d333e" style="color:white"  @click="chooseCardMethods">KART SEÇ</v-btn>
                </div>
               
         </div>
            </div>
           
         </v-card>
      </v-dialog>
  </div>
</template>

<script>
import countDown from '../plugins/countdown'
import colors from '../plugins/colors.json'
import Chat from './chat'
import Bingo from './bingo'
import {listenMyself} from '../plugins/firebase-real-time'
import firebase from '../plugins/firebase-real-time'
import setCard from '../plugins/chooseCard'
import { mapGetters } from 'vuex'
export default {
  computed:{
    ...mapGetters(['transaction','getNumbers','getActiveUser','getUsers','getActiveRoom']),
    userStartGame(){
     return (!this.getActiveUser.admin && !this.transaction.startGame && !this.dialog)
     
    },
    allSelected(){
      let res = false
      let filter = this.getUsers.filter(gu=>gu.bingo=='')
      if(filter.length==0){
        res = true
      }
      else{
        res = false
      }
      return res
    }
  },
  name: 'App',
  components:{
    Chat,
    Bingo
  },
  watch:{
    kickOut(data){
    if(data){
      this.$emit('back',false)
    }
    },
    'transaction.activeNumber'(data){
       this.rndChooseShowNumber = data
    },
    'transaction.chooseNumber'(){
      this.selectNumber()
    },
  },
  data(){
    return{
      username:"",
      startGameDialog:false,
        rndChooseShowNumber:0,
        text:"",
        text2:"",
        text3:"",
        count:3,
      chooseDialog:false,
      user:{},
      showDate: true,
      days:0,
      hours:0,
      minutes:0,
      seconds:0,
      dialog:true,
      colors:"#000",
      colorsHeader:"#000"
    }
  },
 beforeDestroy(){
    
 },
  methods:{
    chooseCardMethods(){
      let cards = setCard()
        this.$api.put('Room/'+this.getActiveRoom.key+'/Users',this.getActiveUser.key,{...this.getActiveUser,bingo:cards.join(',')})
    },
    put(data){
      if(data.startGame){
        this.startGame()
        this.startGameDialog=false
      }
      if(data.chooseNumber){
        this.selectNumber()
      }
      this.$api.putNumber('Room/'+this.getActiveRoom.key+'/transaction',{...this.transaction,...data})
      

    },
     startGame(){
     //   this.getNumbers=[]
        this.setNumbers()
        this.$api.putNumber('Room/'+this.getActiveRoom.key+'/Numbers',{number:this.getNumbers.join(',')}).then(res=>{
            console.log(res);
        })
    },
setNumbers(){
       this.$store.state.numbers = []
for (let i = 1; i < 91; i++) {
       this.$store.state.numbers.push(i)
    }
    },
    listenBingo(){
    
    },
    roomAdmin(){
      let numberx = Math.floor(Math.random() * this.getNumbers.length)
            console.log(numberx);
         
           let pos = this.getNumbers.map(b=>Number(b)).indexOf(numberx)
        console.log(pos);
         this.put({activeNumber:numberx})
         this.rndChooseShowNumber = numberx
       this.$store.commit('setChoosedNumber',pos)
    },
    selectNumber(){
        this.text = "Hazır Mıyız? Numara Çekiliyor.."
        this.text2 = "Sonucu Bekleyin.."
        this.text3 = ""
        this.count = 3
        this.chooseDialog=true
        let vm = this
       let interval = setInterval(() => {
            let number = Math.floor(Math.random() * this.getNumbers.length)
            vm.rndChooseShowNumber = number
        }, 100);
       setTimeout(() => {
             this.text = "Seçilen Numara.."
              this.text2 = "Kağıdın Kontrol Et. Ben Makineyim Hata yapmam ama yine de sen bak adet yerini bulsun."
           if(this.getActiveUser.admin){
              this.roomAdmin()
           }

      
       clearInterval(interval)
       this.$api.putNumber('Room/'+this.getActiveRoom.key+'/Numbers',{number: this.getNumbers.join(",")})
        

         clearInterval(interval)
       setTimeout(() => {
            this.text3 = "Pencere Kapanıyor Lütfen Bekleyin..."
            let count = setInterval(() => {
                vm.count--;
            }, 1000);
              setTimeout(() => {
                 clearInterval(count)
                 this.put({chooseNumber:false})
                
              }, 3000);
       }, 8000);
       }, 5000);
    },
      writeUserData() {
          let admin = false
          if(!this.getActiveRoom.Users){
              admin = true
          }
          else{
             let user = this.getActiveRoom.userData.filter(ud=>ud.admin)
             if(user.length==0){
                 admin=true
             }
             else if(user.length==1 && user[0].key == this.user.key){
                 admin  = true
             }
          }
        if(this.user.key){
          this.$api.put('Room/'+this.getActiveRoom.key+'/Users',this.user.key,{...this.user,name:this.username,tea:false,joke:false,apple:false,admin:admin,key:this.user.key}).then(()=>{
               localStorage.setItem('username',JSON.stringify({...this.user}))
               this.dialog=false
               listenMyself(this.user.key,this.getActiveRoom.key)
          })
        }
        else{
 this.$api.post('Room/'+this.getActiveRoom.key+'/Users',{bingo:"",name:this.username,win:0,loose:0,tea:false,joke:false,apple:false,admin:admin}).then((res)=>{
   this.$api.put('Room/'+this.getActiveRoom.key+'/Users',res.name,{bingo:"",win:0,loose:0,name:this.username,tea:false,joke:false,apple:false,admin:admin,key:res.name})
          console.log(res);
           localStorage.setItem('username',JSON.stringify({bingo:"",name:this.username,won:0,loose:0,key:res.name}))
              listenMyself(res.name,this.getActiveRoom.key)
      this.dialog=false
        })   
        }
       
},
    joinGame(){
      
    }
  },
  mounted(){

      console.log(this.getActiveRoom);
      firebase(this.getActiveRoom.key)
    if(localStorage.getItem('username')){
      this.username = JSON.parse(localStorage.getItem('username')).name
      this.user = JSON.parse(localStorage.getItem('username'))
      this.$store.commit('setActiveUser',this.user)
    }
    let vm = this
    setInterval(() => {
      let rnd = Math.floor(Math.random() * 256)
      vm.colors = colors[rnd].hexString
      vm.colorsHeader = colors[rnd-21].hexString
    let result =  countDown()
    if(result.type=='timeIsUp'){
      vm.showDate = false
    }
    else{
      vm.showDate=true
      vm.days = result.days
      vm.hours = result.hours
      vm.minutes = result.min
      vm.seconds = result.seconds
    }
    }, 1000);
  }
}
</script>

<style>

</style>
