  let firebase = def.default
  console.log(firebase);
  // Initialize Firebase
  const listen = (data,status,room) => {
    console.log('Room/'+room+'/'+data);
    var starCountRef = firebase.database().ref('Room/'+room+'/'+data);
    
    starCountRef.on('value', (snapshot) => {
      const datax = snapshot.val();
       if(!status){
        store.commit(`set${data}`, datax)
       }
       else{
        store.commit(`setActiveUser`, datax)
       }
    });
  }
 export const listenMyself = (key,room)=>{
    listen('Users/'+key,true,room)
 }
 export const listenRoom = ()=>{
    firebase.initializeApp(firebaseConfig);
    firebase.analytics();
    var starCountRef = firebase.database().ref('Room');
    starCountRef.on('value', (snapshot) => {
      const datax = snapshot.val();
        store.commit(`setRooms`, datax)
    });
 }

  export default (room)=> {
      console.log(room);
    listen("transaction",false,room)
    listen("Users",false,room)
    listen("Numbers",false,room)
    listen("Chats",false,room)
    listen("Voting",false,room)
   

  }

