import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        users:[],
        chats:[],
        numbers:[],
        activeUser:{},
        rooms:[],
        transaction:{},
        activeRoom:{},
        kickOut:false,
        voting:[]
      },
      getters:{
        getUsers:(state)=> state.users,
        getVoting:(state)=> state.voting,
        getActiveRoom:(state)=> state.activeRoom,
        getRooms:(state)=> state.rooms,
        transaction:(state)=> state.transaction,
        getChats:(state)=> state.chats,
        kickOut:(state)=> state.kickOut,
        getActiveUser:(state)=> state.activeUser,
        getNumbers:(state)=> state.numbers
      },
    mutations: {
        setVoting(state,payload){
            state.voting = []
            for (const key in payload) {
                console.log(payload[key]);
                    state.voting.push({...payload[key],key:key});  
            }
        },
        setRooms(state,payload){
            state.rooms = []
            for (const key in payload) {
                console.log(payload[key]);
                let users = []
                if(payload[key].Users){
                   
                    for (const key2 in payload[key].Users) {
                        console.log(payload[key2]);
                            users.push({...payload[key].Users[key2],key:key2});  
                    }
                }
                    state.rooms.push({...payload[key],userData:users,key:key,roomNumberCount: payload[key].Numbers.number.split(',')});  
            } 
        },
        setActiveRoom(state,payload){
            state.activeRoom = payload
        },
        settransaction(state,payload){
            state.transaction = payload
        },
        setActiveUser(state,payload){
            state.activeUser = payload
        },
        setChoosedNumber(state,payload){
            console.log(payload);
            state.numbers.splice(payload,1)
        },
        setUsers(state,payload){
            state.kickOut = false
            state.users = []
            let same = false
            for (const key in payload) {
                console.log(payload[key]);
                if(state.activeUser.key == key){
                    same =true
                }
                    state.users.push({...payload[key],key:key});  
            }
            if(!same){
                state.kickOut = true
            }
        },
        setChats(state,payload){
            state.chats = []
            for (const key in payload) {
                console.log(payload[key]);
                    state.chats.push({...payload[key],key:key});  
            }
        },
        setNumbers(state,payload){
            state.numbers = []
                console.log(payload);
                    state.numbers = payload['number'].split(',')
        }
    },
    actions: {
    }
})